<!-- 动态路由茶叶 -->
<template>
  <div>
    <div style="height:170px;width:100%;">
      <img class="imgs" src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTkvMDUvOTIwYWRjMGM1M2U4OGQ2MTY0OWE0MzYwMmY5MWQ0NzQuanBn.jpg" alt="">
    </div>
    <div style="padding:0 3%;height:50px;line-height:50px;background:#f0f0f0;">
        <router-link to="/news" style="font-size:15px;">首页</router-link>>
        <span style="font-size:15px;color:#90af6b;">{{title}}</span>
    </div>
    <div style="padding:60px 40px;">
      <el-row :gutter="10">
        <el-col :xs="24" :sm="16" :md="16">
            <div class="grid-content" style="padding:0 30px 50px 0;">
                <h2 style="font-weight:normal;">{{title}}</h2>
                <p style="margin:10px 0;color:#666;font-size:14px;padding:20px 0;border-bottom:1px #f0f0f0 solid;border-top:1px #f0f0f0 solid;">发布日期：2018-08-07 13:45</p>
                <p style="color:#666;font-size:14px;line-height:24px;padding:20px 0 30px 0;">中国历史上有很长的饮茶纪录，已经无法确切地查明到底是在什么年代了，但是大致的时代是有说法的。并且也可以找到证据显示，确实在世界上的很多地方饮茶的习惯是从中国传过去的。所以，很多人认为饮茶就是中国人首创的，世界上其他地方的饮茶习惯、种植茶叶的习惯都是直接或间接地从中国传过去的。</p>
                <img style="height:460px;" class="imgs" src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNC9iNzQ1ZThkYTQ1NTQ4MjJhYmMyM2EyYTU0ZDY0MmQzYS5qcGc_p_p100_p_3D.jpg" alt="">
                <p style="line-height:24px;font-size:14px;color:#666;padding-top:20px;"> 但是也有人能够找到证据指出，饮茶的习惯不仅仅是中国人发明的，在世界上的其他一些地方也是饮茶的发明地，例如印度、非洲等。1823年，一个英国侵略军的少校在印度发现了野生的大茶树，从而有人开始认定茶的发源地在印度，至少是也在印度。中国当然也有野生大茶树的记载，都集中在西南地区，记载中也包含了甘肃、湖南的个别地区。茶树是一种很古老的双子叶植物，与人们的生活密切相关。</p>
                <p style="line-height:24px;font-size:14px;color:#666;padding-top:20px;">中国饮茶起源众说纷纭：追溯中国人 饮茶的起源，有的认为起于上古，有的认为起于周，起于秦汉、三国、南北朝、唐代的说法也都有，造成众说纷纭的主要原因是因唐代以前无“茶”字，而只有“荼”字的记载，直到茶经的作者陆羽，方将荼字减一画而写成“茶”，因此有茶起源于唐代的说法。其它则尚有起源于神农、起源于秦汉等说法。</p>
                <p style="line-height:24px;font-size:14px;color:#666;padding-top:20px;">发根的生理基础茶树营养器官，具有生理上的自然再生作用。即一部分器官丧失或残缺，在适当条件下，可以生长妯丧失或残缺的部分，而使其本身恢复原来完整的状态。茶树扦插，是利用茶树营养器官自然再生作用和极性现象，剪取茶树半木质化枝条上的茎叶段，将下端插入苗床，经过培育，则能生根抽枝，形成独立的新茶苗。</p>
                <p style="line-height:24px;font-size:14px;color:#666;padding-top:20px;">插穗插入苗床，插穗上下切口处附近的细胞，很快产生薄膜，封闭上下切口。在薄膜之下形成愈伤木栓，覆被切口。愈伤木栓具有防止水分散失和微生物侵入等保护作用。这时下端切口在知宜的温度、湿度下，经过一定时间，则由形成层和韧皮薄壁组织细胞分裂而产生愈伤组织。同时，从中心柱内鞘，或从髓射线和形成层交叉点发出新根。这就是扦插发根的过程。</p>
                <p style="line-height:24px;font-size:14px;color:#666;padding-top:20px;">插穗中 碳水化合物，包括淀粉和糖的含量，与根的形成有密切关系。通常认为，碳水化合物与阑尾炎素化合物的比率（C/N），是生根据的重要因素。碳氮比率大，则有利于发根长苗。实验证明，枝叶变成红棕色，达到半木质化、叶片成熟定型时，碳氮经大，扦插成活率高，发根好，苗木健壮。 扦插发根的生理进程，与插穗所处的湿度、温度、空气、光照和土壤理化状况有密切关系，它们之间互相影响，互有联系。在扦插育过程中，应妥善协调各生态因子间的关系，充分发挥插穗发根的生理特性。</p>
            </div>
        </el-col>
        <el-col :xs="24" :sm="8" :md="8">
            <div class="grid-content" style="padding-left:20px;">
                <h3 style="padding-bottom:20px;font-weight:normal;border-bottom:1px #f0f0f0 solid;">阅读更多文章</h3>
                <p class="news_p" 
                    style="color:#666;border-bottom:1px #f0f0f0 solid;padding:20px 0;" 
                    v-for="(item, index) in arr" :key="index"
                    @click="title=item">
                    <i style="padding:0;font-size:18px;color:#ccc;" class="iconfont icon-youjiantou"></i>
                    <span style="font-size:14px;">{{item}}</span>
                </p>
            </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
export default {
  name:'',
  data(){
   return {
    title:'',
    type:'',
    arr:[
        '饮用碧螺春茶的几个注意事项',
        '碧螺春茶叶喝第几泡才是最好喝的呢?',
        '喝茶养生好处多 但是喝错茶竟会致丧命',
        '喝什么茶好?如何正确喝茶?',
        '喝茶也会有危险——茶这样喝真的很危险',
        '喝茶的好处有哪些 6大好处告诉你',
        '喝什么茶叶好?春天适合喝什么茶?喝白茶...',
        '2019茶叶展-第十六届上海国际茶业交...'
    ]
   }
  },
  created(){
      this.title=this.$route.params.names;
      this.type=this.$route.params.types;
      console.log(this.title)
  },
  methods:{

  }
}
</script>

<style scoped>
.news_p:hover{
  color:#90af6b !important;
}
</style>


