<!-- 动态路由茶叶 -->
<template>
  <div>
    <div style="height:170px;width:100%;">
      <img class="imgs" src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTkvMDUvOTIwYWRjMGM1M2U4OGQ2MTY0OWE0MzYwMmY5MWQ0NzQuanBn.jpg" alt="">
    </div>
    <div style="padding:0 3%;height:50px;line-height:50px;background:#f0f0f0;">
        <router-link to="/tea" style="font-size:15px;">茶叶</router-link>>
        <span style="font-size:15px;color:#90af6b;">{{arr.type}}</span>>
        <span style="font-size:15px;color:#90af6b;">{{arr.info}}</span>
    </div>
    <div style="padding:60px 10%;">
      <div class="clearfix">
        <div v-zidingy="" class="fl" style="width:40%;height:40%;position:relative;">
          <div class="smimg">
            <img class="imgs" :src="arr.img" alt="">
            <span style="position: absolute;left: 0; top: 0;width: 25%;
			          height: 25%;background-color: rgba(255,0,0,.4);display: none;">
            </span>
          </div>
          <div class="big" style="position: absolute;left: 100%;z-index:30;
              top: 0;width: 100%;height: calc(100% - 4px);overflow: hidden;display: none;">
            <img style="position:absolute;top:0;left:0;height:400%;width:400%;" 
              :src="arr.img" alt="">
          </div>
        </div>
        <div class="fl" style="width:57%;margin-left:3%;padding-right:10%;box-sizing:border-box;">
          <h2 id="abc" style="font-weight:normal;font-size:22px;padding-bottom:16px;">{{arr.info}}</h2>
          <p style="font-size:14px;line-height:22px;color:#666;padding:26px 0;">饮茶不但是传统饮食文化，同时，由于茶中含有多种抗氧化物质与抗氧化营养素，对于消除自由基有一定的效果。因此喝茶也有助防老，具养生保健功能，每天喝三两杯茶可起到防老的作用。茶叶中含有多种维生素和氨基酸，喝茶对于清油解腻，增强神经兴奋以及消食利尿也具有一定的作用。</p>
          <p style="font-size:14px;padding:10px 0;color:#666;border-top:1px dotted #ccc;border-bottom:1px dotted #ccc;">商品编号： PECHAM584 </p>
          <p style="font-size:14px;color:#666;padding:10px 0 30px;">分类:    <span style="color:#627f9a;">{{arr.type}}</span></p>
          <div class="clearfix">
            <router-link :to="'/no3/'+arr.uid" style="color:#fff;">
              <el-button @click="zou" class="fl" type="success">
                  加入购物车
              </el-button>
            </router-link>
            <div id="comp" class="fl clearfix" style="margin-left:20px;">
              <span @click='zhong_jian' style="border-right:none;">-</span>
              <span style="border-right:none;">{{arr.num}}</span>
              <span @click='zhong_jia'>+</span>
            </div>
          </div>
        </div>
      </div>
      <div style="margin:20px 0;">
        <img style="height:100px;width:100px;" :src="arr.img" alt="">
      </div>
      <div style="padding-right:40px;">
        <h4 id="box_p" style="border-top:1px #f0f0f0 solid;padding:14px 10px 20px;">描述</h4>
        <p style="color:#666;font-size:15px;line-height:24px;">饮茶不但是传统饮食文化，同时，由于茶中含有多种抗氧化物质与抗氧化营养素，对于消除自由基有一定的效果。因此喝茶也有助防老，具养生保健功能，每天喝三两杯茶可起到防老的作用。茶叶中含有多种维生素和氨基酸，喝茶对于清油解腻，增强神经兴奋以及消食利尿也具有一定的作用。 但并不是喝得越多越好，也不是所有的人都适合喝茶。一般来说，每天1-2次，每次3-5克、400毫升的饮量是比较适当的。一般，人们说喝茶还指喝水</p>
        <img style="margin:10px 0 30px;width:100%;height:100%;" src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNC9iYmM0ZTQxZDFlYjVmNTk2NmE0MzIyYTZhMjhjNDVmZC5qcGc_p_p100_p_3D.jpg" alt="">
      </div>
      <div>
        <p style="padding:20px 0;border-top:1px #f0f0f0 solid;font-size:20px;color:#666;">相关商品</p>
        <img style="height:200px;width:200px;" src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNC8yNjVmNTg1ZTlmY2ZiOTEwMGUzNzViYWQwNDI1OTAzMS0zMDB4MzAwLmpwZw_p_p100_p_3D_p_p100_p_3D.jpg" alt="">
        <p style="line-height:30px;color:#666;">湖北老清茶</p>
        <p style="line-height:30px;color:#666;">￥400.00</p>
      </div>

    </div>
  </div>
</template>

<script>
import {mapActions} from 'vuex'
export default {
  name:'',
  data(){
   return {
     arr:this.$store.state.arr_item,
   }
  },
  watch:{
    '$store.state.arr_item'(a,b){
      this.arr=a;
    }
  },
  methods:{
    zou(){
      console.log(this.arr)
      // this.$store.dispatch('actions名字',实参)
      this.$store.dispatch('a_arr',this.arr)
    },
    //中转的一条数据不用传参了
    ...mapActions(['zhong_jia','zhong_jian','a_arr']) //最后加减好的数据加入购物车
  }
}
</script>

<style scoped>
#abc{
  position:relative;
}
#abc:after{
  content:'';
  position:absolute;
  left:0;
  bottom:0;
  height:2px;
  width:30px;
  background:#ccc;
}
#comp span{
  display: inline-block;
  height:40px;width:28px;
  line-height:40px;
  text-align:center;
  float:left;
  border:1px #ccc solid;
}
#box_p{
  position:relative;
}
#box_p:after{
  content:'';
  left:0;top:0;
  height:4px;width:50px;
  background:#000;
  position: absolute;
}
</style>


