<template>
  <div id="app">
    <!-- 头部 -->
    <el-row style="padding:0 50px;" id="header">
      <el-col :xs="2" :sm="2" :md="2">
        <div class="grid-content">
          <img style="height:50px;width:90px;margin-top:20px;" src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTkvMDQvZjc0MTI0MDFiMDZjZjdjYmI4OTg5MTBjMTE2YzZlNzYucG5n.png" alt="">
        </div>
      </el-col>
      <el-col :offset="8" :xs="11" :sm="11" :md="11">
        <div class="grid-content" style="line-height:80px;">
          <el-row type="flex" class="row-bg" justify="center" style="text-align:center;">
            <el-col :span="3" v-for="(item, index) in navlist" :key="index">
              <div class="grid-content">
                <router-link :class="type==item.path?'active':''" :to="item.path">{{item.title}}</router-link>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-col>
      <el-col :xs="3" :sm="3" :md="3">
        <div class="grid-content" style="line-height:80px;">
          <i @click="dialogVisible = true" class="iconfont icon-sousuo" style="font-size:28px;"></i>
          <router-link to="/login" class="buyao">
            <i class="iconfont icon-wo"></i>
          </router-link>
          <!-- 购物车 -->
          <el-dropdown>
            <router-link to="/no3/0" class="buyao" style="outline:none;">
              <i class="iconfont icon-gouwuche" style="color:#000;"></i>
            </router-link>
            <el-dropdown-menu slot="dropdown">
              <div v-if="arr.length==0" style="padding:20px 50px 20px 0;">
                <el-dropdown-item style="font-size:16px;">购物车里没有商品</el-dropdown-item>
              </div>
              <div v-else >
                <el-dropdown-item class="clearfix" v-for="(item, index) in arr" :key="index">
                  <div style="border-bottom:1px #f0f0f0 solid;padding-top:10px;" class="clearfix">
                    <div class="fl">
                      <img style="height:86px;width:86px;" :src="item.img" alt="">
                    </div>
                    <div class="fl" style="width:200px;margin-left:30px;">
                      <p style="font-size:16px;">{{item.info}} 
                        <i @click="removearr(item)" class="iconfont icon-cuowu1 fr"></i></p>
                      <p>{{item.num}}×￥{{item.price}}</p>
                    </div>
                  </div>
                </el-dropdown-item>
                <div style="text-align:center;padding:16px 0;color:#519f10;">
                  <p >小计:￥{{toprice}}</p>
                  <el-button type="success" style="width:90%;margin-top:10px;">结算</el-button>
                </div>
              </div>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-col>
    </el-row>
    <!-- 搜索 -->
    <el-dialog
      title="搜索"
      :visible.sync="dialogVisible"
      width="60%">
      <span>
        <input type="text" v-model="val" placeholder="请输入关键词"
          style="width:100%;height:50px;font-size:16px;padding-left:20px;box-sizing:border-box;
            background:#f0f0f0;border:none;">
      </span>
      <span slot="footer" class="dialog-footer">
        <router-link :to="{name:'search',query:{names:val}}"> 
          <el-button type="primary" @click="add">确 定</el-button>
        </router-link> 
        <el-button @click="dialogVisible = false">取 消</el-button>
      </span>
    </el-dialog>
    <!-- 内容 -->
    <div id="hides" style="min-height:500px;padding-top:80px;">
      <transition name="nop">
        <router-view/>
      </transition>
    </div>
    <!-- 尾部 -->
    <div>
      <el-row style="padding:60px 30px 40px 30px;background:#f0f0f0;">
        <el-col :xs="2" :sm="2" :md="2">
          <div class="grid-content" style="line-height:30px;">
            <h5 style="font-size:26px;color:#666;font-weight:normal;">联系</h5>
            <span style="font-size:14px;color:#666;">Contact</span>
          </div>
          </el-col>
        <el-col :xs="5" :offset="3" :sm="5" :md="5">
          <div class="grid-content">
            <p style="font-size:18px;margin-bottom:10px;color:#666;">关注我们</p>
            <i style="padding:0;" class="iconfont icon-iconfontweixin"></i>
            <i class="iconfont icon-weibo"></i>
          </div>
        </el-col>
        <el-col :xs="5" :sm="5" :md="5">
          <div class="grid-content" style="color:#666;line-height:26px;font-size:14px;">
            <p style="font-size:18px;margin-bottom:8px;">起飞页自助建站平台</p>
            <p>地址：苏州市建外SOHO东区2号楼</p>
            <p>邮编：100000</p>
            <p>电话：010-69557550</p>
          </div>
        </el-col>
        <el-col :xs="6" :offset="3" :sm="6" :md="6">
          <div class="grid-content" style="text-align:center;font-size:14px;">
            <div class="fl" style="margin:0 20px;">
              <img src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTgvMDgvNjFjNTQ1NWFmY2MyNGViMmZjY2VkOTk3YmMwMWE2MGMtMTAweDEwMC5wbmc_p_p100_p_3D.png" alt="">
              <p style="padding:10px 0;">微信</p>
            </div>
            <div class="fl" style="margin:0 20px;">
              <img src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTgvMDgvNjFjNTQ1NWFmY2MyNGViMmZjY2VkOTk3YmMwMWE2MGMtMTAweDEwMC5wbmc_p_p100_p_3D.png" alt="">
              <p style="padding:10px 0;">微博</p>
            </div>
          </div>
          </el-col>
      </el-row>
      <p style="color:#666;background:#fff;padding:16px 30px;font-size:14px;">COPYRIGHT (©) 2019 茶叶类网站  京ICP备41414141414-1号</p>
    </div>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
export default {
  name: 'App',
  computed:{
    ...mapGetters(['toprice']),
  },
  methods: {
    ...mapActions(['removearr']),
    add(){
      this.dialogVisible = false;
    }
  },
  data(){
    return{
      arr:this.$store.state.arr,
      dialogVisible: false,
      val:'',
      type:'',
      navlist:[
        {
          title:'首页',
          path:'/'
        },
        {
          title:'关于',
          path:'/about'
        },
        {
          title:'茶叶',
          path:'/tea'
        },
        {
          title:'基地',
          path:'/base'
        },
        {
          title:'新闻',
          path:'/news'
        },
        {
          title:'联系',
          path:'/link'
        },
      ]
    }
  },
  created() {
    this.type=this.$route.path;
  },
  watch:{
    '$route'(a,b){
      this.val='';
      this.type=a.path;
    },
    // 'arr'(a,b){
    //   console.log(a,b)
    // }
  }
}
</script>

<style>
*{margin:0;padding:0;-webkit-user-select: none;list-style: none;}
.fl{float:left;}
.fr{float:right;}
.imgs{height:100%;width:100%;}
a{color:#666;text-decoration: none;}
.clearfix:after, .clearfix:before{display:table;content:'';}
.clearfix:after{clear:both;}
.clearfix{zoom:1;}
#header{
  position:absolute;
  left:0;
  top:0;
  width:100%;
  z-index: 10;
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 80px;
}
.grid-content a{
  padding:10px 0;
}
.grid-content a:hover{
  color:#519f10;
  border-bottom:1px #519f10 solid;
}
.active{
  color:#519f10;
  border-bottom:1px #519f10 solid;
}
.grid-content i{
  font-size:22px;
  padding:0 4%;
}
.buyao:hover{
  border:none !important;
  color:#666 !important;
}
.el-dropdown-menu{
  top:50px !important;
  /* right:40px !important; */
}
.nop-enter{
  opacity:0;
}
.nop-enter-active{
  transition:.5s all linear;
}
.nop-enter-to{
  opacity:1;
}
</style>



