import Vue from 'vue'
import Vuex from 'vuex' 

Vue.use(Vuex)

export default new Vuex.Store({
    state:{
        arr:[],
        typearr:[],
        names:'',
        toprice:'',
        arr_item:'',//作为点击商品进页面的中转数据
    },
    getters:{
        arr(state){
            return state.arr
        },
        typearr(state){
            let newarr=[];
            //type类型
            for(var i=0; i<state.arr.length; i++){
                if(newarr.indexOf(state.arr[i].type)==-1){
                    newarr.push(state.arr[i].type)
                }
            }
            return newarr;
        },
        //当前点击的名字
        names(state){
            return state.names;
        },
        //总价
        toprice(state){
            var num=0;
            for(var i=0; i<state.arr.length; i++){
                num+=state.arr[i].num * state.arr[i].price;
            }
            return num;
        }
    },
    mutations:{
        a_arr(state,data){
            console.log(state,data)
            state.names=data.info;
            let type=true;
            //判断有没有点击过商品
            for(var i=0; i<state.arr.length; i++){
                if(state.arr[i].info==data.info){
                    type=false;
                    let nums=data.num;
                    state.arr[i].num+=nums; //这里不能是++
                }
            }
            if(type){
                state.arr.push(data)
            }
        },
        //删除
        removearr(state,data){
            for(let i=0; i<state.arr.length; i++){
                if(state.arr[i].info==data.info){
                    state.arr.splice(i,1)
                }
            }
        },
        //加
        jia(state,data){
            for(let i=0; i<state.arr.length; i++){
                if(state.arr[i].info==data.info){
                    state.arr[i].num++;
                }
            }
        },
        //减
        jian(state,data){
            for(let i=0; i<state.arr.length; i++){
                if(state.arr[i].info==data.info){
                    if(state.arr[i].num<=1) return;
                    state.arr[i].num--;
                }
            }
        },
        //中转数据  只有一条
        zhong(state,data){
            state.arr_item=data;
        },
        //中转数据 加
        zhong_jia(state,data){
            state.arr_item.num++;
        },
        //中转数据 减
        zhong_jian(state,data){
            if(state.arr_item.num<=1) return;
            state.arr_item.num--;
        },
    },
    actions:{
        a_arr(context,data){
            context.commit('a_arr',data);
            // location.replace('#/no3')
        },
        removearr(context,data){
            context.commit('removearr',data);
        },
        jia(context,data){
            context.commit('jia',data);
        },
        jian(context,data){
            context.commit('jian',data);
        },
        zhong(context,data){
            context.commit('zhong',data);
        },
        zhong_jia(context,data){
            context.commit('zhong_jia',data);
        },
        zhong_jian(context,data){
            context.commit('zhong_jian',data);
        },
    }
})




