<template>
  <div>
    <div style="padding:70px 0;background:#f0f0f0;text-align:center;">
      <h2 style="font-size:26px;font-weight:normal;margin-bottom:20px;">产品搜索</h2>
      <div class="clearfix" style="width:55%;padding-left:5%;margin:0 auto;">
        <input
          style="padding-left:20px;font-size:16px;height:40px;border-right:none;border:1px #ccc solid;width:80%"
          class="fl"
          type="text"
          :value="val"
        />
        <input
          style="background:#519f10;color:#fff;height:42px;border:none;width:10%"
          class="fl"
          type="button"
          value="搜索"
        />
      </div>
    </div>
    <div style="min-height:300px;padding:50px;">
      <p style="font-size:26px;">没有符合条件的结果。</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      val: ""
    };
  },
  created() {
    this.val = this.$route.query.names;
  },
  watch: {
    "$route.query.names"(a, b) {
      this.val = a;
    }
  }
};
</script>

<style scoped>
</style>


