<!-- 茶叶 -->
<template>
  <div>
    <el-row style="padding:0 40px;background:#f0f0f0;">
      <el-col :xs="24" :sm="12" :md="12">
        <div class="grid-content">
          <h2 style="line-height:80px;font-weight:normal;">茶叶</h2>
        </div>
      </el-col>
      <el-col :xs="24" :sm="12" :md="12">
        <div class="grid-content">
          <ul id="list" class="list fr" style="padding-right:30px;color:#666;">
            <li
              @click="huan(item,$index)"
              v-for="(item, $index) in list"
              :style="{color:item==type?'#90af6b':''}"
              class="fl"
              style="line-height:80px;padding:0 20px;"
              :key="$index"
            >{{item}}</li>
          </ul>
        </div>
      </el-col>
    </el-row>
    <!-- 内容 -->
    <!-- 过滤 -->
    <div style="padding:30px 40px 0 40px;">
      <div v-show="!newarr.length" style="min-height:400px;text-align:center;">
        <el-button type="primary" :loading="true"
          style="font-size:20px;background:#fff;border:none;color:#000;margin-top:200px;">加载中....</el-button>
      </div>
      <div v-if="type=='全部'">
        <div class="clearfix" style="padding-bottom:50px;">
          <div class="clearfix" style="min-height:400px;">
            <!-- 模拟数据 -->
            <div v-show="imp==1" @click="add(item)" class="box_p" 
              v-for="(item, index) in newarr"  :key="index" style="float:left;margin:10px 30px 18px 20px;">
              <router-link :to="'/tinfo1/'+item.uid">
                <img :src="item.img" alt style="height:240px; width:240px;" />
                <p class="box_ps" style="padding:10px 0 18px 0;color:#666;">{{item.info}}</p>
                <p class="border_p" style="color:#666;border-top:1px #f0f0f0 solid;padding:14px 0;">￥{{item.price}}</p>
                <div class="box_bors" style="color:#ccc;height:30px;width:30px;border:1px #ccc solid;line-height:30px;text-align:center;">
                  <i style="font-size:20px;" class="iconfont icon-youjiantou1"></i>
                </div>
              </router-link>
            </div>
            <div v-show="imp==2" @click="add(item)" class="box_p" v-for="(item, index) in newarr1"  :key="index" style="float:left;margin:10px 30px 18px 20px;">
              <router-link :to="'/tinfo1/'+item.uid">
                <img :src="item.img" alt style="height:240px; width:240px;" />
                <p class="box_ps" style="padding:10px 0 18px 0;color:#666;">{{item.info}}</p>
                <p class="border_p" style="color:#666;border-top:1px #f0f0f0 solid;padding:14px 0;">￥{{item.price}}</p>
                <div class="box_bors" style="color:#ccc;height:30px;width:30px;border:1px #ccc solid;line-height:30px;text-align:center;">
                  <i style="font-size:20px;" class="iconfont icon-youjiantou1"></i>
                </div>
              </router-link>
            </div>
          </div>
          <div id="fenye" style="text-align:center;margin-top:30px;">
            <span @click="huan1" :class="imp==1?'active':''">1</span>
            <span @click="huan2" :class="imp==2?'active':''">2</span>
            <span>....</span>
          </div>
        </div>
      </div>
      <div v-else style="min-height:660px;">
        <div
          v-show="type==item.type"
          v-for="(item, index) in newarr"
          class="box_p"
          style="float:left;margin:10px 30px 0 20px;"
          @click="add(item)"
            :key="index">
            <router-link :to="'/tinfo1/'+item.uid">
              <img :src="item.img" alt style="height:240px; width:240px;" />
