}
.border_p{
  position: relative;
}
.border_p:after{
  width:0;
  height:1px;
  z-index: 5;
  position: absolute;
  left:0; top:0;
  background: #519f10;
  transition: .35s linear all;
  content:'';
}
.box_p:hover .border_p:after{
  width:100%;
}
.box_p:hover .box_ps{
  color:#90af6b !important;
}
.box_p:hover .box_bors{
  color:#90af6b !important;
  border:1px #90af6b solid !important;
}
#fenye span{
  display:inline-block;
  padding:4px 10px;
  font-size:14px;
  border:1px #f0f0f0 solid;
  color:#90af6b !important;
}
.active{
