import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import about from '@/components/about'
import tea from '@/components/tea'
import base from '@/components/base'
import news from '@/components/news'
import link from '@/components/link'
import tinfo from '@/components/tinfo'
import tinfo1 from '@/components/tinfo1'
import search from '@/components/search'
import no3 from '@/components/no3'
import login from '@/components/login'

Vue.use(Router)

export default new Router({
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  },
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/about',
      name: 'about',
      component: about
    },
    {
      path: '/tea',
      name: 'tea',
      component: tea
    },
    {
      path: '/base',
      name: 'base',
      component: base
    },
    {
      path: '/news',
      name: 'news',
      component: news
    },
    {
      path: '/link',
      name: 'link',
      component: link
    },
    {
      path: '/tinfo/:id/:names',
      name: 'tinfo',
      component: tinfo
    },
    {
      path: '/tinfo1/:uid',
      name: 'tinfo1',
      component: tinfo1
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
    {
      path: '/no3/:a',
      name: 'no3',
      component: no3
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
  ]
})



