<!-- 关于 -->
<template>
  <div>
    <div style="height:170px;width:100%;">
      <img class="imgs" src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNS80OWFlMzhhODY3ZWY3NWE0MDczZGY3NGJhMWY5OGMyNi5qcGc_p_p100_p_3D.jpg" alt="">
    </div>
    <div style="padding:60px 40px;">
      <div style="text-align:center;padding:0 12%;border-bottom:1px #f0f0f0 solid;">
        <h3 style="font-size:24px;font-weight:normal;padding:20px 0 30px 0;">春茶</h3>
        <p style="color:#666;padding-bottom:66px;">饮茶不但是传统饮食文化，同时，由于茶中含有多种抗氧化物质与抗氧化营养素，对于消除自由基有一定的效果。
  因此喝茶也有助防老，具养生保健功能，每天喝三两杯茶可起到防老的作用。</p>
      </div>
      <el-row style="padding:80px 0;text-align:center;color:#666;border-bottom:1px #f0f0f0 solid;">
        <el-col :style="{borderRight:index>2?'none':'1px #f0f0f0 solid'}" 
            v-for="(item, index) in arr" :key="index" :xs="24" :sm="12" :md="6" style="border-right:1px #f0f0f0 solid;">
          <p ref="apg" style="font-size:50px;letter-spacing:10px;"></p>
          <p style="font-size:14px;margin-top:6px;">{{item.info}}</p>
        </el-col>
      </el-row>
      <div style="text-align:center;padding:40px 10px 0 10px;">
        <h3 style="font-size:24px;font-weight:normal;padding:20px 0 30px 0;">喝茶的好处</h3>
        <p style="color:#666;padding-bottom:66px;">饮茶不但是传统饮食文化，同时，由于茶中含有多种抗氧化物质与抗氧化营养素，对于消除自由基有一定的效果。因此喝茶也有助防老，具养生保健功能，每天喝三两杯茶可起到防老的作用。茶叶中含有多种维生素和氨基酸，喝茶对于清油解腻，增强神经兴奋以及消食利尿也具有一定的作用。 但并不是喝得越多越好，也不是所有的人都适合喝茶。一般来说，每天1-2次，每次3-5克、400毫升的饮量是比较适当的。一般，人们说喝茶还指喝水。</p>
        <el-row>
          <el-col :xs="24" :sm="24" :md="7" style="margin-left:22px;">
            <div class="border_tf grid-content" style="color:#666;">
              <img class="imgs" src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNC82NzM1MTJkYjY0YzY3ZTQzN2MyMzE0YzJjNzcyNGYzZi5qcGc_p_p100_p_3D.jpg" alt="">
              <h4 style="font-weight:normal;padding:14px 0;">茶园规划</h4>
              <p style="line-height:24px;text-align:left;font-size:14px;">明万历二十三年（1595）张源撰写的《茶录》中，对当时洞庭东西山绿茶（炒青）的采摘、炒制有较为详细的记载，其炒制过程分杀青、轻团、焙干等工序，已与当今洞庭碧螺春的炒制工艺基本一致。</p>
              <div class="border_op" style="transition:.35s all linear;opacity:0;margin:10px auto;height:40px;width:40px;border:1px #ccc solid;"></div>
            </div>
          </el-col>
          <el-col :xs="24" :sm="24" :md="7" :offset="1">
            <div class="border_tf grid-content" style="color:#666;">
              <img class="imgs" src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNC80ZGYwMWE3NGZhMGFlYWI4MjJiYzUxOGFjYzZlYWE5Ni5qcGc_p_p100_p_3D.jpg" alt="">
              <h4 style="font-weight:normal;padding:14px 0;">茶叶品质鉴别</h4>
              <p style="line-height:24px;text-align:left;font-size:14px;">明万历二十三年（1595）张源撰写的《茶录》中，对当时洞庭东西山绿茶（炒青）的采摘、炒制有较为详细的记载，其炒制过程分杀青、轻团、焙干等工序，已与当今洞庭碧螺春的炒制工艺基本一致。</p>
              <div class="border_op" style="transition:.35s all linear;opacity:0;margin:10px auto;height:40px;width:40px;border:1px #ccc solid;"></div>
            </div>
          </el-col>
          <el-col :xs="24" :sm="24" :md="7" :offset="1">
            <div class="border_tf grid-content" style="color:#666;">
              <img class="imgs" src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNC80NWEyYmVlMTVmMTkxNzQ5NDY5MjExMGIyM2RhZGJkNS5qcGc_p_p100_p_3D.jpg" alt="">
              <h4 style="font-weight:normal;padding:14px 0;">冲泡工艺</h4>
              <p style="line-height:24px;text-align:left;font-size:14px;">明万历二十三年（1595）张源撰写的《茶录》中，对当时洞庭东西山绿茶（炒青）的采摘、炒制有较为详细的记载，其炒制过程分杀青、轻团、焙干等工序，已与当今洞庭碧螺春的炒制工艺基本一致。</p>
              <div class="border_op" style="transition:.35s all linear;opacity:0;margin:10px auto;height:40px;width:40px;border:1px #ccc solid;"></div>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name:'',
  data(){
   return {
     arr:[
       {
         n:138,
         info:'历史悠久'
       },
       {
         n:423,
         info:'冲泡温度'
       },
       {
         n:89,
         info:'碧螺春等级'
       },
       {
         n:567,
         info:'茶园面积'
       }
     ]
   }
  },
  mounted(){
    this.$refs.apg.forEach((element,index) => {
		   let i=0
		   let timer=setInterval(() => {
			   i++
			   element.innerHTML=i;
			   if(i>=this.arr[index].n){
				   clearInterval(timer)
			   }
		   },1)
	   });
  }
}
</script>

<style scoped>
.border_tf{
  transition: .35s all linear;
}
.border_tf:hover{
  transform: translateY(-10px);
}
.border_tf:hover .border_op{
  opacity:1 !important;
}
</style>


