<template>
  <div id="no3" style="border-top:1px #f0f0f0 solid;">
      <!-- <p>{{$store.state.typearr}}</p>
      <p>{{$store.state}}</p> -->
      <div v-if="!arr.length" style="text-align:center;padding-top:180px;">
        <p style="margin-bottom:10px;">您的购物车目前是空的</p>
        <router-link to="/tea">
            <el-button type="success">返回商城</el-button>
        </router-link>
      </div>
      <div v-else style="padding:40px 30px;">
        <div style="border:1px #6cc36f solid;padding:10px;line-height:40px;color:#6cc36f;">
            <i class="iconfont icon-duihao1" style="color:#6cc36f;font-size:22px;font-weight:bold;"></i>
            "{{names}}"      成功添加进您的购物车 
            <router-link to="/tea" class="fr">
                <el-button type="success">继续购物</el-button>
            </router-link>
        </div>
        <div class="clearfix">
            <div class="fl" style="width:66%;border-right:1px #f0f0f0 solid;padding-right:30px;box-sizing:border-box;">
                <table style="width:100%;">
                    <thead>
                        <tr style="text-align:center;font-weight:bold;line-height:40px;">
                            <td style="width:50%;text-align:left;">商品</td>
                            <td>价格</td>
                            <td>数量</td>
                            <td>合计</td>
                        </tr>
                    </thead>
                    <tbody v-if="typearr" v-for="i in typearr" :key="i">
                        <tr>
                            <div style="color:#6cc36f;margin:6px 0;background:#f0f0f0; padding:10px;width:196%;">{{i}}</div>
                        </tr>
                        <tr v-if="i==item.type" v-for="(item, index) in arr" :key="index" style="text-align:center;">                           
                            <td style="width:50%;text-align:left;">
                                <i @click="removearr(item)" class="iconfont icon-cuowu" style="font-size:24px;color:#666;"></i>
                                <img :src="item.img" style="vertical-align:middle;margin:0 30px;height:90px;width:90px;" alt="">
                                <span style="color:#6cc36f;">{{item.info}}</span>
                            </td>
                            <td style="color:#666;">￥{{item.price}}</td>
                            <td style="color:#666;">
                                <i @click="jian(item)" class="iconfont icon-plus-minus"></i>
                                <span style="display:inline-block;width:30px;text-align:center;">{{item.num}}</span>
                                <i @click="jia(item)" class="iconfont icon-jia"></i>
                            </td>
                            <td style="color:#666;">￥
                                <span style="display:inline-block;width:30px;text-align:center;">{{item.price * item.num}}</span>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div class="fl gouwu" style="width:34%;padding-left:30px;box-sizing:border-box;">
                <p style="line-height:40px;font-size:18px;font-weight:bold;border-bottom:4px #dfdfdf solid;">购物车总计</p>
                <p style="line-height:30px;">购物车小计<span class="fr">{{toprice}}</span></p>
                <p style="line-height:30px;border-bottom:1px #dfdfdf solid;">运费<span class="fr" style="font-size:14px;">免费送货</span></p>
                <p style="line-height:30px;border-bottom:2px #dfdfdf solid;">订单总计<span class="fr">{{toprice}}</span></p>
                <el-button type="success" style="width:100%;margin:20px 0;">前往收银台</el-button>
                <p>优惠劵:</p>
                <el-button type="success" style="width:100%;margin:10px 0;">使用优惠劵</el-button>
            </div>
        </div>
      </div>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
export default {
  name:'no3',
  computed:{
    ...mapGetters(['arr','typearr','names','toprice']),
  },
  methods:{
    ...mapActions(['removearr','jia','jian']),
  },
}
</script>

<style scoped>
</style>


