var index = {
    install(Vue) {
        //放大镜 在tinfo中
        Vue.directive('zidingy', (a, b) => {
            var oSmBox = a.querySelector('.smimg');
            var oSpan = a.querySelector('.smimg span');
            var oBigBox = a.querySelector('.big');
            var oBigImg = a.querySelector('.big img');
            var mx=0;
            var my=0;
            oSmBox.onmouseover = function(){
                oSpan.style.display = 'block';
                oBigBox.style.display = 'block';
                mx = oSmBox.offsetWidth - oSpan.offsetWidth;
                my = oSmBox.offsetHeight - oSpan.offsetHeight;
            }
            oSmBox.onmouseout = function(){
                oSpan.style.display = 'none';
                oBigBox.style.display = 'none';
            }
            oSmBox.onmousemove = function(e){
                var ev = e || event;
                var x = ev.clientX - oSmBox.getBoundingClientRect().left - oSpan.offsetWidth / 2;
                var y = ev.clientY - oSmBox.getBoundingClientRect().top - oSpan.offsetHeight / 2;
                x<=0?x=0:x;
                y<=0?y=0:y;
                x>=mx?x=mx:x;
                y>=my?y=my:y;
                var sx = x/mx, sy = y/my;
                var max=oBigImg.offsetWidth - oBigBox.offsetWidth;
                oBigImg.style.left = -sx *max + 'px';
                oBigImg.style.top = -sy *max+ 'px';
                oSpan.style.left = x + 'px';
                oSpan.style.top = y + 'px';
            }
        })
    }
}
export default index  


